.wrapper {
	display: inline-block;
	color: #fff;
	font-size: 30px;
}

.header {
	display: none;
}

.firstPart,
.secondPart {
	text-transform: uppercase;
}

.firstPart {
	font-weight: 900;
}

.secondPart {
	font-weight: 200;
}

@media (min-width: 1100px) {
	.header {
		display: inline-block;
	}
}
