.position {
	position: absolute;
	top: 120px;
	left: 100px;
}

.wrapper {
	font-size: 18px;
	color: #aaa;
	display: flex;
	flex-direction: column;
	position: absolute;
	width: 700px;
}

.info {
	display: flex;
	align-items: center;
}

.background {
	width: 100%;
	background-position: center top;
	background-size: cover;
}

.video {
	position: absolute;
	width: 1000px;
	height: 600px;
	background-size: cover;
}

.name {
	color: #fff;
	font-size: 70px;
	font-weight: 900;
	margin-bottom: 10px;
}

.link,
.genres,
.length {
	margin-right: 10px;
}

.genre {
	cursor: pointer;
	transition: all 0.3s;
}

.genre:hover,
.link:hover {
	color: #47c3f0;
}

.link {
	color: #aaa;
}

.ageRestriction {
	border: 1px solid #aaa;
	border-radius: 5px;
	padding: 1px 4px;
}

.description {
	margin-top: 20px;
	margin-bottom: 20px;
	color: #fff;
	font-size: 30px;
	font-weight: 300;
}

.notFound {
	text-align: center;
}
