.footer,
.left,
.middle,
.right {
	display: flex;
	align-items: center;
}

.left,
.right,
.copyright {
	color: #242b2d;
	opacity: 0.4;
}

.middle {
	flex-direction: column;
}

.left {
	margin-left: 50px;
}

.right {
	margin-right: 50px;
}

.footer {
	display: flex;
	align-items: center;
	height: 150px;
	background-color: #fff;
	justify-content: space-between;
}

.copyright {
	position: relative;
	top: 25px;
}

.link,
.icon {
	cursor: pointer;
	transition: all 0.2s;
}

.link {
	font-size: 20px;
	margin-right: 10px;
}

.icon {
	font-size: 24px;
	margin-right: 15px;
}

.link:hover,
.icon:hover {
	color: #0aaee4;
}
